.selected-projects {
    padding: 50px 20px;
    text-align: center;
    background-color: #D9D5D2;
}

.projects {
    display: flex;
    justify-content: space-around;
}

.project {
    flex: 1;
    margin: 10px;
}

.project img {
    /* width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); */
    /* width: 100%; */
    height: 200px; /* Фиксированная высота */
    object-fit: contain; /* Масштабирование изображения до размера, сохраняя пропорции */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
    background-color: #f4f4f4; /* Цвет фона для пустого пространства */
}
.selected-projects h2 {
    font-size: 28px;
    color: #023059;
}

.project h3 {
    font-size: 24px;
    color: #666;
}

.project h4 {
    font-size: 16px;
    color: #666;
}
